export const Projects = () => {
    const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
    const [modalContent, setModalContent] = useState<{
        desktopSrc: string;
        mobileSrc: string;
        src?: string;
        technologies: string;
        title: string;
        text: string
    } | null>(null);

    const [currentIndex, setCurrentIndex] = useState(0);

    //       
    const lockScroll = () => {
        document.body.style.overflow = 'hidden';
    };

    //       
    const unlockScroll = () => {
        document.body.style.overflow = 'auto';
    };

    const handleOpenModal = (project: ProjectType) => {
        const detailedProject = infoProjectDescription.find(p => p.title === project.title);
        if (detailedProject) {
            setModalContent({
                desktopSrc: detailedProject.desktopSrc,
                mobileSrc: detailedProject.mobileSrc,
                src: detailedProject.desktopSrc, //      src
                technologies: detailedProject.technologies,
                title: detailedProject.title,
                text: detailedProject.text
            });
            setIsModalOpen(true);
            lockScroll();
        }
    };

    //     
    const handleCloseModal = () => {
        setIsModalOpen(false);
        setModalContent(null);
        unlockScroll();
    };

    //    (    modalContent.src  modalContent.mobileSrc)
    const images = modalContent
        ? [
            {
                src: modalContent.desktopSrc,   //  
                label: "Desktop"
            },
            {
                src: modalContent.mobileSrc,    //  
                label: "Mobile"
            }
        ]
        :
        []
    ;


    const mSlide = () => {
        setCurrentIndex((prevIndex) =>
            prevIndex === 0 ? images.length - 1 : prevIndex - 1
        );
    };


    //  
    const settings = {
        infinite: true,
        accessibility: true,
        speed: 1000,
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 2400,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    infinite: true,
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };


    return (
        <S.Projects id={'works'}>
            <Container>
                <SectionTitle>Projects</SectionTitle>
                <S.SliderWrapper>
                    <Slider {...settings}>
                        {infoProjectTitle.map((p, index) => (
                            <Project
                                key={index}
                                src={p.src}
                                technologies={p.technologies}
                                title={p.title}
                                text={p.text}
                                buttonText={p.buttonText}
                                onOpenModal={() => handleOpenModal(p)}
                            />
                        ))}
                    </Slider>
                </S.SliderWrapper>
            </Container>
            <ModalOverlayComponent
                isModalOpen={isModalOpen}
                handleCloseModal={handleCloseModal}
                currentIndex={currentIndex}
                mSlide={mSlide}
                images={images}
                modalContent={modalContent}
            />
        </S.Projects>
    );
};




